.app__navbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--color-black);
  padding: 1rem 2rem;

  &-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    img {
      width: 150px;
    }
  }

  &-links {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;

    li {
      margin: 0 1rem;
      cursor: pointer;

      &:hover {
        color: var(--color-grey);
      }
    }
  }

  &-login {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    a {
      margin: 0 1rem;
      text-decoration: none;
      transition: 0.5s ease;
      border-bottom: 1px solid transparent;

      &:hover {
        border-bottom: 1px solid var(--color-golden);
      }
    }

    div {
      width: 1px;
      height: 30px;
      background-color: var(--color-grey);
    }
  }

  &-smallscreen {
    display: none;

    &_overlay {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      background: var(--color-black);
      transition: 0.5s ease;
      flex-direction: column;
      z-index: 5;
    }
    & .overlay__close {
      font-size: 27px;
      color: var(--color-golden);
      cursor: pointer;
      position: absolute;
      top: 20px;
      right: 20px;
    }

    &_links {
      list-style: none;

      li {
        margin: 2rem;
        cursor: pointer;
        color: var(--color-golden);
        font-size: 2rem;
        text-align: center;
        font-family: var(--font-base);

        &:hover {
          color: var(--color-white);
        }
      }
    }
  }
}

@media screen and (max-width: 2000px) {
  .app__navbar {
    &-logo img {
      width: 210px;
    }
  }
}

@media screen and (max-width: 1150px) {
  .app__navbar {
    &-links {
      display: none;
    }

    &-smallscreen {
      display: flex;
    }
  }
}

@media screen and (max-width: 650px) {
  .app__navbar {
    padding: 1rem;

    &-login {
      display: none;
    }

    &-logo img {
      width: 110px;
    }
  }
}
