.app__footer {
    width: 100%;
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    background: var(--color-black);
    padding-top: 0;

    &-links {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-top: 5rem;
        padding: 0 2rem;

        &_contact,
        &_logo,
        &_work {
            flex: 1;
            margin: 1rem;
            text-align: center;
        }

        &_logo img:first-child {
            width: 210px;
            margin-bottom: 0.75rem;
        }

        &_work p:nth-child(2n + 1) {
            margin-bottom: 1rem;
        }

        &_icons {
            margin-top: 0.5rem;

            svg {
                color: var(--color-white);
                margin: 0.5rem;
                font-size: 24px;
                cursor: pointer;

                &:hover {
                    color: var(--color-golden);
                }
            }
        }
    }

    &-headtext {
        font-family: var(--font-base);
        color: var(--color-white);
        font-weight: 400;
        letter-spacing: 0.04em;
        text-transform: capitalize;
        font-size: 32px;
        line-height: 42px;
        margin-bottom: 1rem;
    }
}

.footer__copyright {
    margin-top: 3rem;
}


@media screen and (min-width: 2000px) {
    .app__footer-headtext {
        font-size: 51px;
        line-height: 61px;
    }
}

@media screen and (max-width: 1150px) {
    .app__footer {
        &-links {
            align-items: center;
            flex-direction: column;

            &_contact,
            &_logo,
            &_work {
               margin: 2rem 0;
               width: 100%;
            }
        }
    }
}

@media screen and (max-width: 650px) {
    .app__footer {
        padding: 0 0 2rem 0;
    }
}

@media screen and (max-width: 350px) {
    .app__footer {
        &-links_logo img:first-child {
            width: 80%;
        }
    }
}